import React from 'react';

const  Dropdown = (props) =>{
  
  return(
      <div className="dropdownMenu">
        <div className="selection">
          <select onChange={props.getWeather}>
            <option selected disabled hidden>Select a city</option>
            <option value="5128581">New York</option>
            <option value="2643743">London</option>
            <option value="1275339">Mumbai</option>
            <option value="1850147">Tokyo</option>
            <option value="3882428">Los Angeles</option>
          </select> 
        </div>    
      </div>
  );
}

export default Dropdown;

